import React, { Component } from 'react';

const bankFees = {
  bank: { fixed: 25 },
  epayments: { fixed: 2.6 },
  payoneer: { fixed: 5.15 },
  mono: { fixed: 3 },
  privat: { fixed: 3 },
  paypal: { percent: 4 },
  intel: { percent: 1.4, min: 6 },
  leader: { percent: 2, min: 6 }
};

const marketFees = {
  lavky: {
    usd: 0,
    percent: 0
  },
  ebay: {
    usd: 0.3,
    percent: 10
  },
  etsy: {
    usd: 0.2,
    percent: 5
  }
};

function round (number) {
  return (Math.round(number * 100) / 100).toFixed(2);
}

class App extends Component {
  state = {
      // User input
      product_price: 0,
      product_quantity: 0,
      product_delivery_price: 0,
      product_price_final: 0,
      market: 'lavky',
      output: 'paypal',

      // Calculated values
      comission_market: 0,
      comission_auction: 0,
      comission_total: 0,
      comission_paypal: 0,
      comission_western: 0,
      comission_output: 0,
      cash: 0,
      profit: 0
  };

  onChange = event => this.setState({ [event.target.name]: event.target.value }, this.calculateAll);

  calculateOutputComission = (product_quantity, product_price_final, comission_paypal, comission_western) => {
    const { fixed, percent, min } = bankFees[this.state.output];

    if (fixed) {
      return fixed;
    }

    const comission = (product_quantity * product_price_final - comission_paypal - comission_western) / 100 * percent;

    if (min && comission <= min) {
      return min;
    }

    return comission;
  };

  calculateAll = () => {
      let {
          product_price,
          product_quantity,
          product_delivery_price,
          product_price_final,
          market
      } = this.state;

      product_price = +product_price;
      product_quantity = +product_quantity;
      product_delivery_price = +product_delivery_price;
      product_price_final = +product_price_final;

      const comission_market = (product_quantity * marketFees[market].usd);
      const comission_auction = ((product_quantity * product_price_final) / 100 * marketFees[market].percent);
      const comission_total = (comission_market + comission_auction);
      const comission_paypal = (((product_quantity * product_price_final) / 100 * 1.9) + 0.3);
      const comission_western = (((product_quantity * product_price_final) - comission_paypal) / 100 * 5);
      const comission_output = this.calculateOutputComission(product_quantity, product_price_final, comission_paypal, comission_western);
      const cash = (product_quantity * product_price_final - comission_paypal - comission_western - comission_output);
      const profit = product_quantity * product_price_final - ((product_price + product_delivery_price) * product_quantity) - comission_total - comission_paypal - comission_western - comission_output;

      this.setState({
          comission_market: round(comission_market),
          comission_auction: round(comission_auction),
          comission_total: round(comission_total),
          comission_paypal: round(comission_paypal),
          comission_western: round(comission_western),
          comission_output: round(comission_output),
          cash: round(cash),
          profit: round(profit)
      });
  };

  render() {
    const {
        product_price,
        product_quantity,
        product_delivery_price,
        product_price_final,
        market,
        output,
        comission_market,
        comission_auction,
        comission_total,
        comission_paypal,
        comission_western,
        comission_output,
        cash,
        profit
    } = this.state;

    return (
        <div className="container" id="calculate">
            <div className="left form_half">
                <h4 className="head_primary">Калькулятор</h4>
                <div className="row">
                    <div>Закупочная цена единицы товара ($)</div>
                    <div className="colorful">
                        <input
                            required="required"
                            className="input"
                            name="product_price"
                            value={product_price}
                            onChange={this.onChange}
                            pattern="[0-9\.]{1,}"
                        />
                    </div>
                </div>
                <div className="row">
                    <div>Количество выставленных на продажу товаров (шт.)</div>
                    <div className="colorful">
                        <input
                            required="required"
                            className="input"
                            name="product_quantity"
                            value={product_quantity}
                            onChange={this.onChange}
                            pattern='[0-9\.]{1,}'
                        />
                    </div>
                </div>
                <div className="row">
                    <div>Реальная стоимость пересылки единицы товара ($)</div>
                    <div className="colorful">
                        <input
                            required="required"
                            className="input"
                            name="product_delivery_price"
                            value={product_delivery_price}
                            onChange={this.onChange}
                            pattern="[0-9\.]{1,}"
                        />
                    </div>
                </div>
                <div className="row">
                    <div className="">Ожидаемая продажная цена единицы товара, включая указанную в листинге стоимость
                        пересылки ($)
                    </div>
                    <div className="colorful">
                        <input
                            required="required"
                            className="input"
                            name="product_price_final"
                            value={product_price_final}
                            onChange={this.onChange}
                            pattern="[0-9\.]{1,}"
                        />
                    </div>
                </div>
                <div className="row">
                    <div className="">Маркет для продажи</div>
                    <div className="colorful selectbox">
                        <select required="required" value={market} name='market' onChange={this.onChange} className="input">
                            <option value="ebay">EBAY</option>
                            <option value="etsy">ETSY</option>
                            <option value="lavky">LAVKY</option>
                        </select>
                    </div>
                </div>
            <div className="row">
                <div className="">Способ вывода денег из Western Bid</div>
                <div className="colorful selectbox">
                    <select required="required" name="output" value={output} onChange={this.onChange} className="input">
                        <option value="paypal">PAYPAL</option>
                        <option value="bank">БАНКОВСКИЙ ПЕРЕВОД</option>
                        <option value="intel">ИНТЕЛ-ЭКСПРЕСС (УКРАИНА)</option>
                        <option value="epayments">КАРТА EPAYMENTS</option>
                        <option value="payoneer">КАРТА PAYONEER</option>
                        <option value="mono">КАРТА МОНОБАНКА</option>
                        <option value="privat">КАРТА ПРИВАТБАНКА</option>
                        <option value="leader">КОНТАКТ/ЛИДЕР/ЗОЛОТАЯ КОРОНА</option>
                    </select>
                </div>
            </div>
        </div>
        <div className="right form_half">
                <h4 className="head_primary">
                    Итого
                </h4>
                <div className="row">
                    <div className="">Комиссия маркета за выставление товара ($)</div>
                    <div className="colorful">
                        <input readOnly="readonly" className="input" value={comission_market} />
                    </div>
                </div>
                <div className="row">
                    <div className="">Комиссия аукциона за продажу товара ($)</div>
                    <div className="colorful">
                        <input readOnly="readonly" className="input" value={comission_auction} />
                    </div>
                </div>
                <div className="row">
                    <div className="">Итоговая комиссия аукциона ($)</div>
                    <div className="colorful">
                        <input readOnly="readonly" className="input" value={comission_total} />
                    </div>
                </div>
                <div className="row">
                    <div className="">Kомиссия PayPal ($)</div>
                    <div className="colorful">
                        <input readOnly="readonly" className="input" value={comission_paypal} />
                    </div>
                </div>
                <div className="row">
                    <div className="">Комиссия Western Bid ($)</div>
                    <div className="colorful">
                        <input readOnly="readonly" className="input" value={comission_western} />
                    </div>
                </div>
                <div className="row">
                    <div className="">Комиссия вывода ($)</div>
                    <div className="colorful">
                        <input readOnly="readonly" className="input" value={comission_output} />
                    </div>
                </div>
                <div className="row">
                    <div className="">Получено на&nbsp;руки ($)</div>
                    <div className="colorful">
                        <input readOnly="readonly" className="input" value={cash} />
                    </div>
                </div>
                <div className="row">
                    <div className="">Прибыль от&nbsp;продаж ($)</div>
                    <div className="colorful">
                        <input readOnly="readonly" className="input" value={profit} />
                    </div>
                </div>
            </div>
        </div>
    );
  }
}

export default App;
